}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.modal-close {
  background: var(--primary-white-color);
  cursor: pointer;
  border: 1px solid #0000001a;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-close .icon {
